const serviceItem = [
    {
        heading : " ",
        description : `,    !
             !`,
        iconClass: "fas fa-suitcase-rolling fa-stack-1x fa-inverse"
    },
    {
        heading : " ",
        description : `    !
            !`,
        iconClass: "fas fa-won-sign fa-stack-1x fa-inverse"
    },
    {
        heading : " ",
        description : `   .
                !`,
        iconClass: "fas fa-arrows-alt-h fa-stack-1x fa-inverse"
    },
]

function Services(){
    return (
        <section className="page-section" id="services">
            <div className="container">
                <div className="text-center">
                <h2 className="section-heading text-uppercase">Services</h2>
                <h3 className="section-subheading text-muted">
                           <br />
                       !
                </h3>
                </div>
                <div className="row text-center">
                    {serviceItem.map((item) => 
                        <div className="col-md-4" key={item.heading}>
                            <span className="fa-stack fa-4x">
                                <i className="fas fa-circle fa-stack-2x text-primary"></i>
                                <i className={item.iconClass}></i>
                            </span>
                            <h4 className="my-3">{item.heading}</h4>
                            <p className="text-muted">
                                {item.description}
                            </p>
                        </div>
                    )}                    
                </div>
            </div>
        </section>
    );
}

export default Services;