import logo from "../assets/carryvilly_logo.png"
import { Link, useNavigate } from 'react-router-dom';
import { authService } from "../fbase";

function Navigation({userObj, isLoggedIn}){
    const navigate = useNavigate();
    const onLogout = () => {
        authService.signOut()
        navigate("/", {replace:true})
    };
    return (
        <nav className="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
            <div className="container">
                <Link className="navbar-brand" to={process.env.PUBLIC_URL + "/"} style={{ textDecoration: 'none' }}>
                    <img src={logo} alt="carryvilly" />&nbsp;carryvilly
                </Link>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarResponsive"
                    aria-controls="navbarResponsive"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                Menu
                <i className="fas fa-bars ms-1"></i>
                </button>
                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav text-uppercase ms-auto py-4 py-lg-0">
                        <li className="nav-item" id='home'>
                            <Link to={process.env.PUBLIC_URL + "/"} className="nav-link" style={{ textDecoration: 'none', marginLeft: '0' }}>
                                Home
                            </Link>
                        </li>
                        <li className="nav-item" id='products'>
                            <Link className="nav-link" to={process.env.PUBLIC_URL + "/products"} style={{ textDecoration: 'none' }}>
                                Products   
                            </Link>
                        </li>
                        <li className="nav-item" id='about'>
                            <Link className="nav-link" to={process.env.PUBLIC_URL + "/about"} style={{ textDecoration: 'none' }}>About</Link>
                        </li>
                        {isLoggedIn ?
                            <li className="nav-item" id='profile'>
                                <Link className="nav-link" to={process.env.PUBLIC_URL + "/profile"} style={{ textDecoration: 'none' }}>Profile</Link>
                            </li>
                            :
                            <li className="nav-item" id='signin'>
                                <Link className="nav-link" to={process.env.PUBLIC_URL + "/signin"} style={{ textDecoration: 'none' }}>Sign In</Link>
                            </li>
                        }
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default Navigation;