import data from "../data/products.json"
import { Link } from "react-router-dom"
import { useEffect, useState } from "react";
import getStatus from "./getStatus";

function ProductsList(){
    const [productlist, setProductlist] = useState([]);
    const getProductList = () => {
        setProductlist(data);
    }
    const today = new Date();
    const onChangeList = (e) => {
        if(e.target.value === "small"){
            setProductlist(data.filter(item => item.type === "/"))
        } else if(e.target.value === "medium"){
            setProductlist(data.filter(item => item.type === "/"))
        } else if(e.target.value === "large"){
            setProductlist(data.filter(item => item.type === "/"))
        } else if(e.target.value === "mini"){
            setProductlist(data.filter(item => item.type === ""))
        } else if(e.target.value === "total"){
            setProductlist(data)
        } else if(e.target.value === "able"){
            setProductlist(data.filter(item => item.return_date === null || new Date(item.return_date).getTime() < today.getTime()))
        } else if(e.target.value === "unable"){
            setProductlist(data.filter(item => new Date(item.return_date).getTime() >= today.getTime()))
        } 
    }
    useEffect(()=>{
        getProductList()
    }, []);
    return (
        <section className="page-section bg-light" id="products">
            <div className="container">
                <div className="text-center">
                    <h2 className="section-heading text-uppercase">products</h2>
                    <select onChange={onChangeList} className="productsListSelect">
                        <option value="total"></option>
                        <option value="small">/(20)</option>
                        <option value="medium">/(24)</option>
                        <option value="large">/(28)</option>
                        <option value="able"> </option>
                        <option value="unable"> </option>
                        <option value="mini">(14)</option>
                    </select>
                    <div className="row">
                        {productlist.map((product) => (
                            <div className="col-lg-3 col-sm-6 mb-4" key={product.id}>
                                <div className="product-item" id="product">
                                    <Link
                                        className="product-link"
                                        to={process.env.PUBLIC_URL + `/product-detail/${product.id}`}
                                    >
                                        <div className="product-hover">
                                            <div className="product-hover-content">
                                                <i className="fas fa-plus fa-3x"></i>
                                            </div>
                                        </div>
                                        <div className={getStatus(product.reserv_date, product.return_date, product.repair, product.damage)}>{getStatus(product.reserv_date, product.return_date, product.repair, product.damage)}</div>
                                        <img
                                            className="img-fluid"
                                            src={product.imgSrc[0]}
                                            alt={product.id.slice(0,4)}
                                            loading="lazy"
                                        />
                                    </Link>
                                    <Link
                                        className="product-link"
                                        to={process.env.PUBLIC_URL + `/product-detail/${product.id}`}
                                    >
                                        <div className="product-caption">
                                            <div className="product-caption-heading">No.{product.num}</div>
                                            <h5>{product.name}</h5>
                                            <div className="product-caption-subheading text-muted">
                                                {product.type}/{product.size}
                                            </div>
                                        </div>  
                                    </Link>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ProductsList;