import { useEffect, useState } from "react";
import Contact from "../components/Contact";
import Navigation from "../components/Navigation";
import useTitle from "@nooks/use-title";
import { authService } from "../fbase";
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from "firebase/auth"
import { useNavigate } from "react-router-dom";

function SignIn(){
    const navigate = useNavigate()
    useTitle(" ")
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [newAccount, setNewAccount] = useState(true)
    const [error, setError] = useState("");
    const onChange = (event) => {
        const { target : {name, value}} = event;
        if(name === "email"){
            setEmail(value)
        } else if(name==="password"){
            setPassword(value)
        }
    }
    const onSubmit = async(event) => {
        event.preventDefault();
        try{
            let data;
            const auth = getAuth();
            if(newAccount){
                data = await createUserWithEmailAndPassword(
                    auth, email, password
                );
            } else {
                data = await signInWithEmailAndPassword(auth, email, password);
            }
            console.log(data)
            navigate("/", {replace:true})
        } catch(error) {
            setError(error.message)
        }
    }
    const toggleAccount = () => setNewAccount((prev) => !prev)
    const onSocialClick = async (event) => {
        const {target:{name}} = event;
        let provider;
        if(name === "google"){
            provider = new GoogleAuthProvider()
        }
        const data = await signInWithPopup(authService, provider)
        console.log(data)
        navigate("/", {replace:true})
    }
    return (
        <div>
            <Navigation />
            <br />
            <br />
            <br />
            <section className="page-section" id="signin">
                <div className="container">
                    <div className="text-center">
                        <h2 className="section-heading text-uppercase">Sign In</h2>
                    </div>
                    <div className="row text-center">
                        <form onSubmit={onSubmit}>
                            <input type="text" name="email" onChange={onChange} placeholder="Email" value={email} required />
                            <br />
                            <input type="password" name="password" onChange={onChange} placeholder="Password" value={password} required />
                            <br />
                            <input type="submit" value={newAccount ? "" : ""} />
                            <div>{error}</div>
                        </form>
                        <div>
                            <button onClick={toggleAccount}>
                                {newAccount ? "" : ""}
                            </button>
                            <button onClick={onSocialClick} name="google"> </button>
                        </div>
                    </div>
                </div>
            </section>
            <Contact />
        </div>
    )
}

export default SignIn;