import { useEffect, useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from 'react-router-dom';
import Price from './Price.js'
import getStatus from './getStatus.js';

function Productinfo({product}){
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    useEffect(() => {
        handleSelect();
    }, []);    
    return (
        <div
            className="product-modal"
            role="dialog"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-5">
                                <h2 className="section-heading text-uppercase">product</h2>
                                <h3>No.{product.num} <br/>{product.name}</h3>
                                    <Carousel activeIndex={index} onSelect={handleSelect} indicators={true}>
                                        { product.imgSrc && product.imgSrc.map((img) => (
                                            <Carousel.Item key={img}>
                                                <img
                                                    className="d-block w-100"
                                                    src={img}
                                                    alt={img}
                                                />
                                            </Carousel.Item>
                                        ))
                                        }
                                    </Carousel>
                                    <table border="solid">
                                        <caption>* ,     .( )</caption>
                                        <tbody>
                                            <tr>
                                                <th></th>
                                                <td>{product.num}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.name}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td><strong>{getStatus(product.reserv_date, product.return_date, product.repair, product.damage)}</strong></td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.type}/{product.size}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.zip ? "" : ""}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.extension ? "" : ""}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.wheels}</td>
                                            </tr>
                                            <tr>
                                                <th></th>
                                                <td>{product.sidehandle ? "" : ""}</td>
                                            </tr>
                                            {
                                                function(){
                                                    if(product.return_date === null || product.repair || product.damage){
                                                        return null;
                                                    } else {
                                                        return (
                                                            <tr>
                                                                <th></th>
                                                                <td>{product.return_date}</td>
                                                            </tr>
                                                        )
                                                    }
                                                }()
                                            }
                                            {product.desc && 
                                                <tr>
                                                    <th></th>
                                                    <td>{product.desc}</td>
                                                </tr>
                                            }
                                        </tbody>
                                    </table>
                                    {(function(){if(product.type === ""){
                                        return (<p>*      .(+2,000)</p>)
                                    } else if(product.repair || product.damage){
                                        return (<p>*   .</p>)
                                    } else {
                                        return (
                                            <Link
                                                to={process.env.PUBLIC_URL + `/product-order/${product.id}`}
                                            >
                                                <button
                                                    className="btn btn-primary btn-xl text-uppercase"
                                                    type="button"
                                                >
                                                    
                                                </button>
                                            </Link>
                                        )
                                    }})()}
                            </div>
                        </div>
                    </div>
                </div>
                <Price />
            </div>
        </div>
    )
}

export default Productinfo;