import {
    BrowserRouter as Router,
    Routes,
    Route
} from "react-router-dom";
import Home from "../routes/Home";
import Products from "../routes/Products";
import About from "../routes/About";
import ProductDetail from "../routes/ProductDetail";
import Order from "../routes/Order";
import SignIn from "../routes/SignIn";
import Profile from "../routes/Profile";

function AppRouter({userObj, isLoggedIn}) {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <Routes>
                {isLoggedIn ?
                    <>
                        <Route path="/" element={<Home userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/products/" element={<Products userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/product-detail/:id" element={<ProductDetail userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/product-order/:id" element={<Order userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/about/" element={<About userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/profile/" element={<Profile userObj={userObj} isLoggedIn={isLoggedIn} />} />
                    </>
                    :
                    <>
                        <Route path="/" element={<Home userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/products/" element={<Products userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/product-detail/:id" element={<ProductDetail userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/product-order/:id" element={<Order userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/about/" element={<About userObj={userObj} isLoggedIn={isLoggedIn} />} />
                        <Route path="/signin/" element={<SignIn />} />
                    </>
                }
            </Routes>
        </Router>
    );
}

export default AppRouter;