import styles from './Ordertable.module.css'
import { useEffect, useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import 'react-datepicker/dist/react-datepicker.css';
import { ko } from "date-fns/esm/locale";
import { addDays, differenceInDays } from 'date-fns';
import { DateRange } from 'react-date-range';
import 'react-date-range/dist/styles.css';
import 'react-date-range/dist/theme/default.css';
import swal from 'sweetalert';
import { Link } from "react-router-dom"

function Ordertable({product}){
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();
    
        emailjs.sendForm("carryvilly", "template_4imxwbm", form.current, "QHRm4EKaGVeosJWZR")
            .then((result) => {
                swal(" !", " !","success").then((okay)=>{
                    window.location.replace(process.env.PUBLIC_URL + "/products/");
                })
            }, (error) => {
                swal("   ...", "", "error");
            });
    };

    const [inputs, setInputs] = useState({
        cname: "",
        cnumber: "",
        size: "",
        name: "",
        email: "",
        phone: "",
        destination: "",
        address: ""
    });

    const {cname, cnumber, size, name, email, phone, destination, address} = inputs;

    useEffect(()=>{
        setInputs({
            ...inputs,
            cname: product.name,
            cnumber: product.num,
            size: `${product.type}/${product.size}`,
        })
    }, [product.name, product.num, product.type, product.size])

    const onChange = (e) => {
        const {name, value} = e.target;
        setInputs({
            ...inputs,
            [name]: value,
        })
    }

    const [minDate, setMinDate] = useState(new Date())
    const getMinDate = (returnDate) => {
        if(returnDate === null || new Date(returnDate).getTime() < new Date().getTime()){
            setMinDate(new Date);
        } else {
            setMinDate(new Date(returnDate))
        }
    }
    console.log(minDate)
    console.log(new Date(product.return_date))

    useEffect(()=>{
        getMinDate(product.return_date)
    }, [product.return_date])

    const options = {
        day: 'numeric',
        month: 'long', 
        year: 'numeric',
        weekday: 'short'
    };

    const [state, setState] = useState([
        {
            startDate: minDate,
            endDate: addDays(minDate, 7),
            key: "selection",
        }
    ])

    const firstDay = new Intl.DateTimeFormat("kr-KO", options).format(state[0].startDate);
    const lastDay = new Intl.DateTimeFormat("kr-KO", options).format(state[0].endDate);

    const [days, setDays] = useState(differenceInDays(state[0].endDate, state[0].startDate));
    const [term, setTerm] = useState(`${firstDay} ~ ${lastDay}`)

    useEffect(()=>{
        setDays(differenceInDays(state[0].endDate, state[0].startDate));
        setTerm(`${firstDay} ~ ${lastDay}`)
    }, [state])

    console.log(state)

    const [rentaltime, setRentaltime] = useState("12:00:00")
    const getRentaltime = (event) => {
        setRentaltime(event.target.value);
    }

    const [mini, setMini] = useState(false);
    const getMini = () => {
        !mini ? setPrice((current) => current + 2000)
            : setPrice((current) => current - 2000)
        setMini(!mini);        
    }

    const [price, setPrice] = useState(7000);
    useEffect(()=>{
        if(days <= 7){
            setPrice(7000)
            setMini(false)
        } else if(days > 7 && days <= 14){
            setPrice(13000)
            setMini(false)
        } else if(days > 14 && days <= 21){
            setPrice(20000)
            setMini(false)
        } else {
            setPrice(27000)
            setMini(false)
        }
    }, [days])
    return (
        <section className="page-section" id="order">
            <div className="container">
                <div className="text-center">
                    <h2 className="section-heading text-uppercase">order</h2>
                    <div className="row text-center">
                        <form ref={form} onSubmit={sendEmail}>
                            <table border="solid" className={styles.orderTable}>
                                <thead>
                                    <tr>
                                        <td rowSpan="3" width="25%">{product.imgSrc? <Link to={process.env.PUBLIC_URL + `/product-detail/${product.id}`}><img src={product.imgSrc[0]} alt={product.id} className={styles.thumbnail} /></Link>:null}</td>
                                        <th width="25%"></th>
                                        <td>
                                            <input type="text" value={`No.${cnumber}`} name="cnumber" className={styles.passiveInfo} readOnly />
                                            <br />
                                            <input type="text" value={cname} name="cname" className={styles.passiveInfo} readOnly />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th></th>
                                        <td>
                                            <input type="text" value={`${size}`} name="size" className={styles.passiveInfo} readOnly />
                                        </td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <th>*</th>
                                        <td colSpan="2">
                                            <input
                                                type="text"
                                                name="name"
                                                placeholder=""
                                                id="username"
                                                className={styles.input}
                                                onChange={onChange}
                                                value={name}
                                                required
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th>*</th>
                                        <td colSpan="2">
                                            <input
                                                type="email"
                                                name="email"
                                                placeholder="example@mail.com"
                                                id="useremail"
                                                className={styles.input}
                                                value={email}
                                                onChange={onChange}
                                                required
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th>*</th>
                                        <td colSpan="2">
                                            <input
                                                type="tel"
                                                name="phone"
                                                placeholder="010-1234-5678"
                                                id="userphone"
                                                className={styles.input}
                                                value={phone}
                                                onChange={onChange}
                                                required
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan="3"><p className={styles.p}>*  , ,   .</p></td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh}>*</th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input
                                                type="text"
                                                name="destination"
                                                placeholder="ex), "
                                                id="destination"
                                                className={styles.input}
                                                value={destination}
                                                onChange={onChange}
                                                required
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh} colSpan="3">*</th>
                                    </tr>
                                    <tr>
                                        <td className={styles.orderTd} colSpan="3">
                                        <DateRange
                                            ranges={state}
                                            locale={ko}
                                            minDate={new Date()}
                                            onChange={(item) => {
                                                setState([item.selection])
                                            }}
                                            required
                                        />
                                        </td>
                                    </tr>
                                    <tr className={styles.none}>
                                        <th className={styles.orderTh}></th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input type="text" name="term" value={term} readOnly />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh}></th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input type="text" name="days" className={styles.days} value={days} readOnly />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh}></th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input
                                            type="time"
                                            id="time"
                                            name='time'
                                            className="dateTime"
                                            value={rentaltime}
                                            min="10:00:00"
                                            max="22:00:00"
                                            onChange={getRentaltime}
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan="3">
                                            <p className={styles.p}>
                                                * ,   ,    !
                                                <br />
                                                *      . 
                                                  !
                                            </p>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh}></th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input
                                                type="checkbox"
                                                name="mini"
                                                id="mini"
                                                className={styles.radioBtn}
                                                onChange={getMini}
                                                value={mini ? "(+2,000)" : "  "}
                                            /><label htmlFor="mini"> (+2,000)</label>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh}></th>
                                        <td className={styles.orderTd} colSpan="2">
                                            <input type="text" id={styles.price} value={price} name="price" onChange={onChange} readOnly />
                                        </td>
                                    </tr>
                                    <tr>
                                        <th className={styles.orderTh} colSpan="3">*</th>
                                    </tr>
                                    <tr>
                                        <td colSpan="3">
                                            <input
                                                type="text"
                                                name="address"
                                                id={styles.address}
                                                onChange={onChange}
                                                value={address}
                                                placeholder="    "
                                                required
                                            />
                                            <p>
                                                *        .
                                                <br/>
                                                *   ,   .
                                                <br/>
                                                *    ,  .
                                            </p>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan="3">
                                            <input type="submit" name="submit" className="btn" value="" />
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Ordertable;